import * as React from "react";
import { TagItem } from "../tags/tag";
import Attachment, { AttachmentItem } from "./Attachment";
import AttachmentSelect from "./AttachmentSelect";
import { _t } from "../../../utils/Translator";
import TagSelect from "../tags/TagSelect";

export interface AttachmentsProps {
  attachments: AttachmentItem[];
  tags: TagItem[];
}

export interface AttachmentsState {
  shown_atts: AttachmentItem[];
}

export default class Attachments extends React.Component<AttachmentsProps, AttachmentsState> {
  attachmentRefs: Attachment[];
  constructor(props, context) {
    super(props, context);
    this.state = {
      shown_atts: [],
    };
    this.attachmentRefs = [];
  }

  resetAttachments = () => {
    this.setState({ shown_atts: [] });
  };

  onAttachmentRemove = (attachment: AttachmentItem) => {
    this.setState({
      shown_atts: this.state.shown_atts.filter(function (item) {
        return item !== attachment;
      }),
    });
  };

  onAttachmentSelected = (attachment: AttachmentItem) => {
    this.setState({
      shown_atts: this.state.shown_atts.concat([attachment]),
    });
  };

  onAllAttachmentsSelected = () => {
    this.setState({
      shown_atts: this.props.attachments,
    });
  };

  onTagAllSelected = (tag: TagItem) => {
    for (let attRef of this.attachmentRefs) {
      attRef.tagsRef.current.onTagSelected(tag);
    }
  };

  getAttachmentsData = () => {
    let attachments_data = [];
    for (let attRef of this.attachmentRefs) {
      attachments_data.push(attRef.getAttachmentData());
    }
    return attachments_data;
  };

  getTagAllSelect = () => {
    if (this.state.shown_atts.length > 1) {
      return (
        <TagSelect
          text={_t("Add tag to all")}
          onTagSelected={this.onTagAllSelected}
          tags={this.props.tags}
        ></TagSelect>
      );
    } else {
      return null;
    }
  };

  render() {
    this.attachmentRefs = [];
    let attachments = [];
    for (let attachment of this.state.shown_atts) {
      attachments.push(
        <Attachment
          attachment={attachment}
          tags={this.props.tags}
          onAttachmentRemove={this.onAttachmentRemove}
          key={attachment.id}
          ref={(node) => {
            if (node) {
              this.attachmentRefs.push(node);
            }
          }}
        ></Attachment>
      );
    }
    let select_attachments = this.props.attachments.filter((item) => {
      return !this.state.shown_atts.includes(item);
    });
    return (
      <div>
        <div className="page-section-title">{_t("Attachments")}</div>
        <div className="mt-3">
          <button
            className="btn btn-outline-primary btn-sm"
            type="button"
            onClick={this.onAllAttachmentsSelected}
          >
            <i className="fa fa-plus-circle" aria-hidden="true"></i>&nbsp;{_t("Add all")}
          </button>
          <button
            className="btn btn-outline-primary btn-sm ms-2"
            type="button"
            onClick={this.resetAttachments}
          >
            <i className="fa fa-minus-circle" aria-hidden="true"></i>&nbsp;{_t("Remove all")}
          </button>
        </div>
        <div className="mt-2">
          {this.getTagAllSelect()}
        </div>
        <div className="mt-3">
          {attachments}
        </div>
        <div className="mt-3">
          <AttachmentSelect
            attachments={select_attachments}
            onAttachmentSelected={this.onAttachmentSelected}
          ></AttachmentSelect>
        </div>
      </div>
    );
  }
}
