import * as React from "react";
import { TagItem } from "../tags/tag";
import SearchSelect, { SearchItem, SearchSelectProps, SearchSelectState } from "./SearchSelect";

export interface SearchTagItem extends SearchItem {
  tags: TagItem[];
}

export interface SearchTagSelectProps extends SearchSelectProps {
  items: SearchTagItem[];
  selectedItem: SearchTagItem;
  onItemSelected: (i: SearchTagItem) => void;
}

export default class SearchTagSelect extends SearchSelect<SearchTagSelectProps> {
  filterItem = (item: SearchTagItem, searchInput: string) => {
    if (this.props.selectedItem && item.id == this.props.selectedItem.id) {
      return false;
    } else if (item.name.toLowerCase().includes(searchInput)) {
      return true;
    } else {
      let tagMatch = false;
      for (let tag of item.tags) {
        tagMatch = tagMatch || tag.name.toLowerCase().includes(searchInput);
      }
      return tagMatch;
    }
  };

  getTagsHTML = (tags: TagItem[]) => {
    let tagElements = [];
    for (let tag of tags) {
      tagElements.push(<div key={tag.id} className="badge bg-primary rounded-pill me-2">{tag.name}</div>)
    }
    return (
      <div className="mb-2">
        {tagElements}
      </div>
    );
  };

  getItemHTML = (item: SearchTagItem) => {
    return (
      <a className="dropdown-item" href="#">
        <div>{item.name}</div>{this.getTagsHTML(item.tags)}
      </a>
    );
  };
}