import React, { useState, useEffect } from "react";
import { _t } from "../../../utils/Translator";

interface ProgressProps {
  processing: boolean;
}

const Progress: React.FC<ProgressProps> = ({ processing }) => {
  const [progress, setProgress] = useState<number>(0);

  useEffect(() => {
    if (processing) {
      setProgress(0); // Reset progress when processing starts
      const interval = setInterval(() => {
        setProgress((prev) => Math.min(prev + 1, 100)); // Increment progress by 1 every 200ms
      }, 200);
      return () => clearInterval(interval); // Cleanup interval on stop
    }
    return undefined;
  }, [processing]);

  return (
    <>
      {processing && (
        <div className="modal fade show" style={{ display: "block" }} tabIndex={-1}>
          <div className="modal-dialog modal-fullscreen modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-body page">
                <div className="d-flex align-items-center justify-content-center h-100">
                  <div className="w-100 text-center">
                    <i className="fa fa-spin fa-spinner fa-5x text-primary"></i>
                    <h1 className="mb-5">{_t("I'm on the case!")}</h1>
                    <p>{_t("Please wait while I complete your request.")}</p>
                    <div className="progress" style={{ height: "38px" }}>
                      <div
                        className="progress-bar progress-bar-striped progress-bar-animated bg-primary"
                        role="progressbar"
                        style={{ width: `${progress}%` }}
                        aria-valuenow={progress}
                        aria-valuemin={0}
                        aria-valuemax={100}
                      >
                        {progress}%
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="modal-backdrop fade show"></div>
        </div>
      )}
    </>
  );
};

export default Progress;
