import React, { FC } from "react";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import SearchSelect from "../searchselect/SearchSelect";

export interface TaskItem {
  name: string;
  id: number;
}

export interface TaskProps {
  tasks: TaskItem[];
  selectedTask: TaskItem;
  onTaskSelected: (t: TaskItem) => void;
}

const Task: FC<TaskProps> = ({ tasks, selectedTask, onTaskSelected }) => {
  const getTaskButton = () => {
    if (selectedTask) {
      const url = `${api.baseURL}/web#id=${selectedTask.id}&model=project.task&view_type=form`;
      return (
        <div className="task-link">
          <a href={url} target="_blank" rel="noreferrer">
            <i className="fa fa-external-link link-like-button" aria-hidden="true"></i>
          </a>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="task-outer-container">
      <div className="task-title">
        <div className="page-section-title">{_t("Task")}</div>
        {getTaskButton()}
      </div>
      <SearchSelect
        items={tasks}
        selectedItem={selectedTask}
        onItemSelected={onTaskSelected}
        selectText={_t("Select task")}
        searchText={_t("Search task")}
      />
    </div>
  );
};

export default Task;