import React, { FC } from "react";

export interface ErrorProps {
  error: string;
  setError: (error: string) => void;
}

const Error: FC<ErrorProps> = ({ error, setError }) => {
  if (!error) return null;

  return (
    <div className="alert alert-danger mt-3 mb-3 alert-dismissible fade show" role="alert">
      {error}
      <button
        title="Close"
        type="button"
        className="btn-close"
        data-bs-dismiss="alert"
        onClick={() => setError("")}
      ></button>
    </div>
  );
};

export default Error;