import React, { useState, useEffect, useContext } from "react";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import api from "../../api";
import { CaseItem } from "../case/Case";
import { _t, saveTranslations, translationsExpired } from "../../../utils/Translator";
import ComposeMainPage from "../composepages/ComposeMainPage";
import { MailTemplateItem } from "../mail_template/MailTemplate";
import Footer from "../footer/Footer";
import Error from "../error/Error";
import Header, { UserInfo } from "../header/Header";
import AddPartyOrDocumentPage from "../composepages/AddPartyOrDocumentPage";
import GenerateMailPage from "../composepages/GenerateMailPage";
import GenerateBasicAiTextPage from "../composepages/GenerateBasicAiTextPage";
import GenerateAdvancedAiTextPage from "../composepages/GenerateAdvancedAiTextPage";
import { PromptTemplateItem } from "../prompt_template/PromptTemplate";

const getConnectionToken = () => {
  return "Bearer " + localStorage.getItem("odooConnectionToken");
};

export enum ComposeMainPages {
  ComposeMainPage,
  AddPartyOrDocumentPage,
  GenerateMailPage,
  GenerateBasicAiTextPage,
  GenerateAdvancedAiTextPage,
}

type ComposeMainProps = {
  disconnect: () => void;
};

const ComposeMain: React.FC<ComposeMainProps> = (props: ComposeMainProps) => {
  const { disconnect } = props;
  const [page, setPage] = useState<ComposeMainPages>(ComposeMainPages.ComposeMainPage);
  const [error, setError] = useState<string>("");
  const [cases, setCases] = useState<CaseItem[]>([]);
  const [selectedCase, setSelectedCase] = useState<CaseItem>(null);
  const [mailTemplates, setMailTemplates] = useState<MailTemplateItem[]>([]);
  const [userInfo, setUserInfo] = useState<UserInfo>(null);
  const [promptTemplates, setPromptTemplates] = useState<PromptTemplateItem[]>([]);
  const [selectedpromptTemplate, setSelectedpromptTemplate] = useState<PromptTemplateItem>(null);

  const getTranslations = async () => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getTranslations,
        ContentType.Json,
        getConnectionToken(),
        { plugin: "outlook" },
        true
      );
      response = JSON.parse(await request.promise);
      saveTranslations(response.result);
    } catch (error) {
      setError(String(error));
    }  
  };

  const getComposeData = async () => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getComposeData,
        ContentType.Json,
        getConnectionToken(),
        {},
        true
      );
      response = JSON.parse(await request.promise); 
    } catch (error) {
      setError(String(error));
    }
    return response; 
  };

  const getData = async () => {
    const response = await getComposeData()
    if (response.result.success) {
      setCases(response.result.data.cases);
      setMailTemplates(response.result.data.mail_templates);
      setPromptTemplates(response.result.data.prompt_templates);
      setUserInfo(response.result.data.user_info);
    } else {
      setError(response.result.error);
    };
  }

  useEffect(() => {
    getData();
    if (translationsExpired()) {
        getTranslations();
    }   
  }, []);

  let composeMainPage = (
    <ComposeMainPage 
      goToPage={setPage} 
      userInfo={userInfo}
    />
  );

  let addPartyOrDocumentPage = (
    <AddPartyOrDocumentPage 
      cases={cases} 
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      goToPage={setPage}
      getConnectionToken={getConnectionToken}
    />
  );

  let generateMailPage = (
    <GenerateMailPage 
      mailTemplates={mailTemplates} 
      getConnectionToken={getConnectionToken} 
      goToPage={setPage} 
    />
  );

  let generateBasicAiTextPage = (
    <GenerateBasicAiTextPage 
      getConnectionToken={getConnectionToken} 
      goToPage={setPage} 
    />
  );

  let generateAdvancedAiTextPage = (
    <GenerateAdvancedAiTextPage 
      getConnectionToken={getConnectionToken} 
      cases={cases}
      selectedCase={selectedCase}
      onCaseSelected={setSelectedCase}
      promptTemplates={promptTemplates}
      selectedPromptTemplate={selectedpromptTemplate}
      onPromptTemplateSelected={setSelectedpromptTemplate}
      goToPage={setPage} 
    />
  );

  let wrapPage = (page) => (
    <>
      <Header userInfo={userInfo} />
      <div className="page">
        <Error error={error} setError={setError} />
        {page}
      </div>
      <Footer disconnect={disconnect} />
    </>
  );

  switch (page) {
    case ComposeMainPages.ComposeMainPage:
      return wrapPage(composeMainPage);
    case ComposeMainPages.AddPartyOrDocumentPage:
      return wrapPage(addPartyOrDocumentPage);
    case ComposeMainPages.GenerateMailPage:
      return wrapPage(generateMailPage);
    case ComposeMainPages.GenerateBasicAiTextPage:
      return wrapPage(generateBasicAiTextPage);
    case ComposeMainPages.GenerateAdvancedAiTextPage:
      return wrapPage(generateAdvancedAiTextPage);
    default:
      return composeMainPage;
  }

};

export default ComposeMain;