import React, { FC } from "react";

export interface FooterProps {
  disconnect: () => void;
}

const Footer: FC<FooterProps> = ({ disconnect }) => {
  const getConnectButton = () => (
    <a
      title="logout"
      role="button"
      onClick={disconnect}
      key="logout"
    >
      <i className="fa text-primary fa-sign-out" aria-hidden="true"></i>
    </a>
  );

  return (
    <footer className="text-muted fixed-bottom bg-light">
      <span>
        Copyright&nbsp;
        <i className="fa fa-copyright" aria-hidden="true"></i>&nbsp;
        {new Date().getFullYear()}&nbsp;Innoverius
      </span>
      <span className="float-end">{getConnectButton()}</span>
    </footer>
  );
};

export default Footer;
