import React, { FC } from "react";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import SearchSelect from "../searchselect/SearchSelect";

export interface PromptTemplateItem {
  id: number;
  name: string;
  prompt: string;
}

export interface PromptTemplateProps {
  promptTemplates: PromptTemplateItem[];
  selectedPromptTemplate: PromptTemplateItem;
  onPromptTemplateSelected: (c: PromptTemplateItem) => void;
}

const PromptTemplate: FC<PromptTemplateProps> = ({
  promptTemplates,
  selectedPromptTemplate,
  onPromptTemplateSelected,
}) => {
  const getPromptTemplateButton = () => {
    if (selectedPromptTemplate) {
      const url = `${api.baseURL}/web#id=${selectedPromptTemplate.id}&model=artificial_intelligence.template&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    }
    return null;
  };

  return (
    <div>
      <div className="page-section-title">
        {_t("Prompt Template")}&nbsp;{getPromptTemplateButton()}
      </div>
      <SearchSelect
        items={promptTemplates}
        selectedItem={selectedPromptTemplate}
        onItemSelected={onPromptTemplateSelected}
        selectText={_t("Select prompt template")}
        searchText={_t("Search prompt template")}
      />
    </div>
  );
};

export default PromptTemplate;