import * as React from "react";
import Tag, { TagItem } from "./tag";
import TagSelect from "./TagSelect";
import { _t } from "../../../utils/Translator";

export interface TagsProps {
  tags: TagItem[];
}

export interface TagsState {
  selected_tags: TagItem[];
}

export default class Tags extends React.Component<TagsProps, TagsState> {
  constructor(props, context) {
    super(props, context);
    this.state = {
      selected_tags: [],
    };
  }

  resetTags = () => {
    this.setState({ selected_tags: [] });
  };

  getTagsData = () => {
    return this.state.selected_tags;
  };

  onTagClicked = (tag: TagItem) => {
    this.setState({
      selected_tags: this.state.selected_tags.filter(function (item) {
        return item !== tag;
      }),
    });
  };

  onTagSelected = (tag: TagItem) => {
    if (!this.state.selected_tags.includes(tag)) {
      this.setState({
        selected_tags: this.state.selected_tags.concat([tag]),
      });
    }
  };

  render() {
    let tags = [];
    for (let tag of this.state.selected_tags) {
      tags.push(<Tag onTagClicked={this.onTagClicked} tag={tag} key={tag.id}></Tag>);
    }
    let select_tags = this.props.tags.filter((item) => {
      return !this.state.selected_tags.includes(item);
    });
    return (
      <>
        <TagSelect text={_t("Add tag")} onTagSelected={this.onTagSelected} tags={select_tags}></TagSelect>
        <div className="mt-2">
          {tags}
        </div>
      </>
    );
  }
}
