import * as React from "react";
import { TagItem } from "./tag";

export interface TagSelectProps {
  text: string;
  tags: TagItem[];
  onTagSelected: (tag: TagItem) => void;
}

export default class TagSelect extends React.Component<TagSelectProps> {
  private getOptions = (tags: TagItem[]) => {
    let options = [];
    options.push(
      <option value="" key={""} disabled hidden>
        {this.props.text}
      </option>
    );
    for (let t of tags) {
      let name = t.name;
      let id = t.id;
      options.push(
        <option value={id} key={id}>
          {name}
        </option>
      );
    }
    return options;
  };

  tag_onselect = (event) => {
    let select = event.target;
    let selected = select.options[select.selectedIndex];
    if (selected.value) {
      let tag = this.props.tags.find((val) => {
        return val.id == selected.value;
      });
      if (tag) {
        this.props.onTagSelected(tag);
      }
    }
  };

  render() {
    let options = this.getOptions(this.props.tags);
    let selection = (
      <select title="tags" className="form-select mt-3" onChange={this.tag_onselect} value="">
        {options}
      </select>
    );
    return selection;
  }
}
