import * as React from "react";

export interface TagItem {
  name: string;
  id: number;
}

export interface TagProps {
  tag: TagItem;
  onTagClicked: (tag: TagItem) => void;
}

export default class Tag extends React.Component<TagProps> {
  tag_onclick = () => {
    this.props.onTagClicked(this.props.tag);
  };

  render() {
    const { tag } = this.props;
    return (
      <div className="badge bg-primary rounded-pill me-2" onClick={this.tag_onclick}>
        {tag.name}&nbsp;<i className="fa fa-times" aria-hidden="true"></i>
      </div>
    );
  }
}
