import React, { useState } from "react";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { CaseItem } from "../case/Case";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import Task, { TaskItem } from "../task/Task";
import Error from "../error/Error";
import { ReadMainPages } from "../readmain/ReadMain";
import Progress from "../progress/Progress";

export interface TimesheetInfo {
  minDuration: number;
}

interface SaveTimesheetPageProps {
  goToPage: (page: ReadMainPages) => void;
  selectedCase: CaseItem;
  timesheetInfo: TimesheetInfo;
  description: string;
  getConnectionToken: () => string;
}

const formatTimeString = (t: string) => {
  let [hours = "00", minutes = "00"] = t.split(":");
  hours = hours.padStart(2, "0");
  minutes = minutes.padStart(2, "0");
  return `${hours}:${minutes}`;
};

const minutesToStimeString = (m: number) => {
  let hours = Math.floor(m / 60);
  let minutes = m - hours * 60;
  let timeString = formatTimeString(hours.toString() + ":" + minutes.toString());
  return timeString;
};

const SaveTimesheetPage: React.FC<SaveTimesheetPageProps> = ({ goToPage, selectedCase, timesheetInfo, description: initialDescription, getConnectionToken }) => {
  const [error, setError] = useState<string>("");
  const [selectedTask, setSelectedTask] = useState<TaskItem | null>(null);
  const [time, setTime] = useState<string>(minutesToStimeString((timesheetInfo && timesheetInfo.minDuration) || 0));
  const [nonBillable, setNonBillable] = useState<boolean>(false);
  const [description, setDescription] = useState<string>(initialDescription);
  const [processing, setProcessing] = useState<boolean>(false);

  const saveTimesheet = async (selectedTask: TaskItem, selectedCase: CaseItem, time: string, nonBillable: boolean, description: string) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveTimesheet,
        ContentType.Json,
        getConnectionToken(),
        {
          task_data: selectedTask,
          case_data: selectedCase,
          time,
          non_billable: nonBillable,
          description,
        },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const onSaveTimesheet = async () => {
    setProcessing(true);
    const response = await saveTimesheet(selectedTask, selectedCase, time, nonBillable, description);
    if (response.result.success) {
      goToPage(ReadMainPages.SaveMailPage);
    } else {
      setError(response.result.error);
    };
    setProcessing(false);
  };

  const getSaveTimesheetButton = () => (
    <button title="Save timesheet" type="button" className="btn btn-primary" onClick={onSaveTimesheet}>
      {_t("Save")}
    </button>
  );

  const getCancelButton = () => (
    <button title="Return" type="button" className="btn btn-default" onClick={() => goToPage(ReadMainPages.SaveMailPage)}>
      <i className="fa fa-arrow-left text-primary"></i>
    </button>
  );

  const getTimeSection = () => (
    <>
      <div className="page-section-title">{_t("Time")}</div>
      <div>
        <div className="form-check">
          <input
            className="form-check-input"
            type="checkbox"
            onChange={(e) => setNonBillable(e.target.checked)}
          />
          <label className="form-check-label">Non-billable</label>
        </div>
        <input
          className="time-input form-control"
          type="time"
          value={time}
          onChange={(e) => setTime(e.target.value)}
        />
      </div>
    </>
  );

  const getDescriptionSection = () => (
    <>
      <div className="page-section-title">{_t("Description")}</div>
      <input
        className="name-input form-control"
        type="text"
        value={description}
        onChange={(e) => setDescription(e.target.value)}
      />
    </>
  );

  const tasks = selectedCase ? selectedCase.tasks : [];

  return (
    <div>
      <div className="page-title"><i className="fa fa-clock-o fa-fw text-primary"></i>&nbsp; {_t("Save Timesheet")}</div>
      <Error error={error} setError={setError} />
      <Task selectedTask={selectedTask} tasks={tasks} onTaskSelected={setSelectedTask} />
      {getTimeSection()}
      {getDescriptionSection()}
      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        <span>{getSaveTimesheetButton()}</span>
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default SaveTimesheetPage;