import React, { useState, useRef } from "react";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import Case from "../case/Case";
import Mail, { MailItem } from "../mail/Mail";
import Attachments from "../attachments/Attachments";
import { CaseItem } from "../case/Case";
import { TagItem } from "../tags/tag";
import { AttachmentItem } from "../attachments/Attachment";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import Error from "../error/Error";
import { ReadMainPages } from "../readmain/ReadMain";
import Progress from "../progress/Progress";

interface SaveMailPageProps {
  onMailSaved: (selectedCase: CaseItem) => void;
  goToPage: (page: ReadMainPages) => void;
  cases: CaseItem[];
  mail: MailItem;
  tags: TagItem[];
  attachments: AttachmentItem[];
  getConnectionToken: () => string;
}

const SaveMailPage: React.FC<SaveMailPageProps> = ({ onMailSaved, goToPage, cases, mail, tags, attachments, getConnectionToken }) => {
  const [error, setError] = useState<string>("");
  const [selectedCase, setSelectedCase] = useState<CaseItem | null>(null);
  const [processing, setProcessing] = useState<boolean>(false);

  const mailRef = useRef<any>(null);  // Using useRef to create a reference
  const attachmentsRef = useRef<any>(null);

  const onCaseSelected = (c: CaseItem) => {
    setSelectedCase(c);
  };

  const saveMail = async (caseData: CaseItem, mailData: MailItem, attachmentsData: AttachmentItem[]) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.saveMail,
        ContentType.Json,
        getConnectionToken(),
        { case_data: caseData, mail_data: mailData, attachments_data: attachmentsData },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error)); 
    }  
    return response; 
  };

  const onResetPage = () => {
    setSelectedCase(null);
    setProcessing(false);
    mailRef.current.resetMail();
    attachmentsRef.current.resetAttachments();
  };

  const onSaveMail = async () => {
    setProcessing(true);
    const caseData = selectedCase;
    const mailData = await mailRef.current.getMailData();
    const attachmentsData = await Promise.all(attachmentsRef.current.getAttachmentsData());
    const response = await saveMail(caseData, mailData, attachmentsData);
    if (response.result.success) {
      onMailSaved(caseData);
      goToPage(ReadMainPages.SaveTimesheetPage);
    } else {
      setError(response.result.error);
    };
    setProcessing(false);
  };

  const getSaveMailButton = () => (
    <button title="Confirm" type="button" className="btn btn-primary" onClick={onSaveMail}>
      {_t("Confirm")}
    </button>
  );
  

  const getResetPageButton = () => (
    <button title="Reset" type="button" className="btn btn-default" onClick={onResetPage}>
      <i className="fa fa-refresh text-primary"></i>
    </button>
  );

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-cloud-upload fa-fw text-primary"></i>&nbsp;{_t("Save E-mail")}
      </div>
      <Error error={error} setError={setError} />
      <Case cases={cases} selectedCase={selectedCase} onCaseSelected={onCaseSelected} />
      {mail && <Mail ref={mailRef} mail={mail} tags={tags} />}
      {attachments && <Attachments ref={attachmentsRef} attachments={attachments} tags={tags} />}
      <div className="d-flex justify-content-between page-button-container">
        <span>{getResetPageButton()}</span>
        <span>{getSaveMailButton()}</span>
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default SaveMailPage;