import React, { useState } from "react";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { TagItem } from "../tags/tag";
import SearchTagSelect from "../searchselect/SearchTagSelect";
import Error from "../error/Error";

/* global Office */

export interface DocumentItem {
  name: string;
  filename: string;
  id: number;
  caseId: number;
  tags: TagItem[];
  hasPdf: boolean;
}

interface DocumentProps {
  documents: DocumentItem[];
  selectedDocument: DocumentItem;
  onDocumentSelected: (p: DocumentItem) => void;
  getConnectionToken: () => string;
}

const Document: React.FC<DocumentProps> = ({ documents, selectedDocument, onDocumentSelected, getConnectionToken }) => {
  const [error, setError] = useState<string>("");
  const getDocumentData = async (document: DocumentItem, getPdf: boolean) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getDocumentData,
        ContentType.Json,
        getConnectionToken(), 
        { document_id: document.id, get_pdf: getPdf },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const addDocument = async (document: DocumentItem, getPdf: boolean) => {
    let response = await getDocumentData(document, getPdf);
    if (response) {
      Office.context.mailbox.item.addFileAttachmentFromBase64Async(
        response.result.data.datas, 
        response.result.data.filename
      );
    }
  };

  const getAddDocumentButton = () => (
    <div
      role="button"
      id="addDocument"
      className="btn btn-outline-primary btn-sm"
      onClick={() => addDocument(selectedDocument, false)}
    >
      <i className="fa fa-plus-circle"></i>&nbsp;{_t("Original")}
    </div>
  );

  const getAddDocumentAsPdfButton = () => (
    <div
      role="button"
      id="addDocumentAsPdf"
      className="btn btn-outline-primary btn-sm ms-2"
      onClick={() => addDocument(selectedDocument, true)}
    >
      <i className="fa fa-plus-circle"></i>&nbsp;{_t("Pdf")}
    </div>
  );

  const getDocumentButtons = () => {
    if (selectedDocument) {
      return (
        <>
          {getAddDocumentButton()}
          {selectedDocument.hasPdf && getAddDocumentAsPdfButton()}
        </>
      );
    }
    return null;
  };

  const getDocumentButton = () => {
    if (selectedDocument) {
      const url = `${api.baseURL}/web#id=${selectedDocument.id}&model=cases.document&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    } else {
      return null;
    }
  };

  return (
    <div>
      <div className="page-section-title">{_t("Document")}&nbsp;{getDocumentButton()}</div>
      <Error error={error} setError={setError}/>
      <SearchTagSelect
        items={documents}
        selectedItem={selectedDocument}
        onItemSelected={onDocumentSelected}
        selectText={_t("Select document")}
        searchText={_t("Search document")}
      />
      <div className="mt-3">
        {getDocumentButtons()}
      </div>
    </div>
  );
};

export default Document;