import React, { FC, useState } from "react";
import api from "../../api";

export interface UserInfo {
  avatar_128: string;
  user_name: string;
  company_name: string;
  ai_level: string;
}

export interface HeaderProps {
  userInfo: UserInfo | null;
}

const Header: FC<HeaderProps> = ({ userInfo }) => {
  const [isAvatarValid, setIsAvatarValid] = useState(true);

  if (!userInfo) {
    return (
      <header className="d-flex align-items-center justify-content-between bg-light">
        <div className="d-flex align-items-center">
          <span className="fa-stack fa-2x me-3">
            <i className="fa fa-square fa-stack-2x"></i>
            <i className="fa fa-spinner fa-spin fa-stack-1x fa-inverse"></i>
          </span>
          <div>
            <div className="header-title mb-0">User loading...</div>
            <div className="mb-0 text-muted">Company loading...</div>
          </div>
        </div>
      </header>
    );
  }

  return (
    <header className="d-flex align-items-center justify-content-between bg-light">
      <div className="d-flex align-items-center">
        {isAvatarValid ? (
          <img
            src={`data:image/jpeg;base64,${userInfo.avatar_128}`}
            height="48px"
            alt="User"
            className="rounded me-3"
            onError={() => setIsAvatarValid(false)}
          />
        ) : (
          <span className="fa-stack fa-2x me-3">
            <i className="fa fa-square fa-stack-2x"></i>
            <i className="fa fa-user fa-stack-1x fa-inverse"></i>
          </span>
        )}
        <div>
          <div className="header-title mb-0">{userInfo.user_name}</div>
          <div className="mb-0 text-muted">
            {userInfo.company_name}&nbsp;
            <a href={api.baseURL} target="_blank" rel="noreferrer">
              <i className="fa fa-external-link" aria-hidden="true"></i>
            </a>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;