import React from "react";
import { _t } from "../../../utils/Translator";
import SearchSelect from "../searchselect/SearchSelect";
import api from "../../api";

export interface MailTemplateItem {
  name: string;
  id: number;
}

interface MailTemplateProps {
  mailTemplates: MailTemplateItem[];
  selectedMailTemplate: MailTemplateItem;
  onMailTemplateSelected: (t: MailTemplateItem) => void;
}

const MailTemplate: React.FC<MailTemplateProps> = ({
  mailTemplates,
  selectedMailTemplate,
  onMailTemplateSelected,
}) => {
  
  const getMailTemplateButton = () => {
    if (selectedMailTemplate) {
      const url = `${api.baseURL}/web#id=${selectedMailTemplate.id}&model=mail.template&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    } else {
      return null;
    }
  };

  return (
    <div>
      <div className="page-section-title">{_t("Mail Template")}&nbsp;{getMailTemplateButton()}</div>
      <SearchSelect
        items={mailTemplates}
        selectedItem={selectedMailTemplate}
        onItemSelected={onMailTemplateSelected}
        selectText={_t("Select mail template")}
        searchText={_t("Search mail template")}
      />
    </div>
  );
};

export default MailTemplate;