import App, { Mode } from "./components/App";
import { AppContainer } from "react-hot-loader";
import { initializeIcons } from "@fluentui/font-icons-mdl2";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { Authenticator } from "@microsoft/office-js-helpers";

/* global document, Office, module, require */

initializeIcons();

let isOfficeInitialized = false;
const mode = Mode.Read;

const render = (Component) => {
  ReactDOM.render(
    <AppContainer>
      <Component mode={mode} isOfficeInitialized={isOfficeInitialized} itemChangedRegister={itemChangedRegister} />
    </AppContainer>,
    document.getElementById("container")
  );
};

let itemChangedHandler: (type: Office.EventType) => void;
const itemChangedRegister = (f: (type: Office.EventType) => void) => {
  itemChangedHandler = f;
};

/* Render application after Office initializes */
Office.onReady(() => {
  if (Authenticator.isAuthDialog()) return;
  isOfficeInitialized = true;
  Office.context.mailbox.addHandlerAsync(Office.EventType.ItemChanged, itemChangedHandler);
  render(App);
});

/* Initial render showing a progress bar */
render(App);

if ((module as any).hot) {
  (module as any).hot.accept("./components/App", () => {
    const NextApp = require("./components/App").default;
    render(NextApp);
  });
}
