import React from "react";
import { _t } from "../../../utils/Translator";
import { ComposeMainPages } from "../composemain/ComposeMain";
import { UserInfo } from "../header/Header";

interface ComposeMainPageProps {
  goToPage: (page: ComposeMainPages) => void;
  userInfo: UserInfo;
}

const ComposeMainPage: React.FC<ComposeMainPageProps> = ({ goToPage, userInfo }) => {

  const getAddPartyOrDocumentButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(ComposeMainPages.AddPartyOrDocumentPage)}
      >
        <i className="fa fa-envelope-open fa-fw text-primary"></i>&nbsp; {_t("Add Party/Document")}
      </button>
    );
  };
  
  const getGenerateMailButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(ComposeMainPages.GenerateMailPage)}
      >
        <i className="fa fa-pencil-square-o fa-fw text-primary"></i>&nbsp; {_t("Generate Email")}
      </button>
    );
  };

  const getGenerateBasicAiTextButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(ComposeMainPages.GenerateBasicAiTextPage)}
      >
        <i className="fa fa-magic fa-fw text-primary"></i>&nbsp; {_t("Generate Text")}
      </button>
    );
  };

  const getGenerateAdvancedAiTextButton = () => {
    return (
      <button
        type="button"
        className="text-start btn btn-default w-100"
        onClick={() => goToPage(ComposeMainPages.GenerateAdvancedAiTextPage)}
      >
        <i className="fa fa-magic fa-fw text-primary"></i>&nbsp; {_t("Generate Text")}
      </button>
    );
  };

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-home fa-fw text-primary"></i>&nbsp;{_t("Home")}
      </div>
      <div className="card">
        <div className="card-body">
          <div className="page-section-title">{_t("Case Management")}</div>
          {getAddPartyOrDocumentButton()}
        </div>
      </div>
      <div className="card">
        <div className="card-body">
          <div className="page-section-title">{_t("Email Automation")}</div>
          {getGenerateMailButton()}
        </div>
      </div>
      <div className="card">
        <div className="card-body">
          <div className="page-section-title">{_t("Artificial Intelligence")}</div>
          {userInfo?.ai_level === "basic" ? getGenerateBasicAiTextButton() : getGenerateAdvancedAiTextButton()}
        </div>
      </div>
    </div>
  );
};

export default ComposeMainPage;