import * as React from "react";
import { AttachmentItem } from "./Attachment";
import { _t } from "../../../utils/Translator";

export interface AttachmentSelectProps {
  attachments: AttachmentItem[];
  onAttachmentSelected: (attachment: AttachmentItem) => void;
}

export default class AttachmentSelect extends React.Component<AttachmentSelectProps> {
  private getOptions = (attachments: AttachmentItem[]) => {
    let options = [];
    options.push(
      <option value="" key={""} disabled hidden>
        {_t("Add attachment")}
      </option>
    );
    for (let a of attachments) {
      let name = a.name;
      let id = a.id;
      options.push(
        <option value={id} key={id}>
          {name}
        </option>
      );
    }
    return options;
  };

  attachment_onselect = (event) => {
    let select = event.target;
    let selected = select.options[select.selectedIndex];
    if (selected.value) {
      let attachment = this.props.attachments.find((val) => {
        return val.id == selected.value;
      });
      if (attachment) {
        this.props.onAttachmentSelected(attachment);
      }
    }
  };

  render() {
    let options = this.getOptions(this.props.attachments);
    let selection = (
      (options.length > 1) ? (
      <select className="form-select" onChange={this.attachment_onselect} value="">
        {options}
      </select>) : ( <></> )
    );
    return selection;
  }
}
