import { _t } from "../../../utils/Translator";
import Case from "../case/Case";
import { CaseItem } from "../case/Case";
import Party, { PartyItem } from "../party/Party";
import Document, { DocumentItem } from "../document/Document";
import { ComposeMainPages } from "../composemain/ComposeMain";
import React, { useEffect, useState } from "react";

interface AddPartyOrDocumentPageProps {
  cases: CaseItem[];
  selectedCase: CaseItem;
  onCaseSelected: (m: CaseItem) => void;
  goToPage: (page: ComposeMainPages) => void;
  getConnectionToken: () => string;
}

const AddPartyOrDocumentPage: React.FC<AddPartyOrDocumentPageProps> = ({ cases, selectedCase, onCaseSelected, goToPage, getConnectionToken }) => {
  const [selectedParty, setSelectedParty] = useState<PartyItem | null>(null);
  const [selectedDocument, setSelectedDocument] = useState<DocumentItem | null>(null);

  const getCancelButton = () => {
    return (
      <button title="Return" type="button" className="btn btn-default" onClick={() => goToPage(ComposeMainPages.ComposeMainPage)}>
        <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
      </button>
    );
  };

  return (
    <div>
      <div className="page-title"><i className="fa fa-envelope-open fa-fw text-primary"></i>&nbsp; {_t("Compose E-mail")}</div>
      <Case cases={cases} selectedCase={selectedCase} onCaseSelected={onCaseSelected} />
      {selectedCase && (
        <>
          <Party
            parties={selectedCase.parties}
            selectedParty={selectedParty}
            onPartySelected={setSelectedParty}
          />
          <Document
            documents={selectedCase.documents}
            selectedDocument={selectedDocument}
            onDocumentSelected={setSelectedDocument}
            getConnectionToken={getConnectionToken}
          />
        </>
      )}
      <div className="page-button-container">
        <span>{getCancelButton()}</span>
      </div>
    </div>
  );
};

export default AddPartyOrDocumentPage;