import * as React from "react";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { ComposeMainPages } from "../composemain/ComposeMain";
import { useState } from "react";
import Case, { CaseItem } from "../case/Case";
import PromptTemplate, { PromptTemplateItem } from "../prompt_template/PromptTemplate"; 
import Error from "../error/Error";
import Progress from "../progress/Progress";

enum MessageRole {
  System = "system",
  Assistant = "assistant",
  User = "user",
}

interface Message {
  role: MessageRole;
  content: string;
}

interface GenerateAdvancedAiTextPageProps {
  getConnectionToken: () => string;
  cases: CaseItem[];
  selectedCase: CaseItem;
  onCaseSelected: (c: CaseItem) => void;
  promptTemplates: PromptTemplateItem[];
  selectedPromptTemplate: PromptTemplateItem;
  onPromptTemplateSelected: (i: PromptTemplateItem) => void;
  goToPage: (page: ComposeMainPages) => void;
}

const GenerateAdvancedAiTextPage: React.FC<GenerateAdvancedAiTextPageProps> = (
  props: GenerateAdvancedAiTextPageProps
) => {
  const {
    getConnectionToken,
    cases,
    selectedCase,
    onCaseSelected,
    promptTemplates,
    selectedPromptTemplate,
    onPromptTemplateSelected,
    goToPage,
  } = props;
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);
  const [selection, setSelection] = useState<string>("");
  const [communication, setCommunication] = useState<boolean>(true);
  const [prompt, setPrompt] = useState<string>("");
  const [history, setHistory] = useState<Message[]>([]);
  const [chatId, setChatId] = useState<number>(0);
  const [loadPromptTemplate, setLoadPromptTemplate] = useState<boolean>(false);
  const [addCase, setAddCase] = useState<boolean>(false);
  const [addedCase, setAddedCase] = useState<boolean>(false);

  const getResponse = async (
    prompt: string,
    messages: Message[],
    chatId: number | null,
    caseId: number | null
  ) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.generateAdvancedAiText,
        ContentType.Json,
        getConnectionToken(),
        { prompt: prompt, messages: messages, chat_id: chatId, case_id: caseId },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response;
  };

  const onSend = async () => {
    setProcessing(true);
    // Use selection as context
    checkSelection();
    let promptWithSelectedText = prompt;
    if (selection) {
      promptWithSelectedText = "Input: " + '"' + prompt + '"\n' + "Context: " + '"' + selection + '"';
    }
    // Use communication as context
    let messages = [];
    if (communication) {
      messages = history.concat([{ role: MessageRole.User, content: promptWithSelectedText }]);
    } else {
      messages = messages.concat([{ role: MessageRole.User, content: promptWithSelectedText }]);
    }
    // Get the response
    const response = await getResponse(
      promptWithSelectedText,
      messages,
      chatId != 0 && chatId,
      addedCase && selectedCase && selectedCase.id
    );
    if (response.result.success) {
      setChatId(Number(response.result.data.chat_id));
      setHistory(
        history.concat([
          { role: MessageRole.User, content: promptWithSelectedText },
          { role: MessageRole.Assistant, content: response.result.data.response },
        ])
      );
      setPrompt("");
    } else {
      setError(response.result.error);
    }
    setProcessing(false);
  };

  const onAddCase = () => {
    if (!addCase) {
      setAddCase(true);
      setAddedCase(false);
    } else {
      onRemoveCase();
    }
  };

  const onAddedCase = () => {
    setAddedCase(true);
    setAddCase(false);
  };

  const onRemoveCase = () => {
    setAddedCase(false);
    setAddCase(false);
  };

  const onLoadPromptTemplate = () => {
    if (!loadPromptTemplate) {
      setLoadPromptTemplate(true);
    } else {
      onRemovePromptTemplate();
    }
  };

  const onLoadedPromptTemplate = () => {
    setLoadPromptTemplate(false);
    setPrompt(selectedPromptTemplate.prompt);
  };

  const onRemovePromptTemplate = () => {
    setLoadPromptTemplate(false);
  };

  const checkSelection = async () => {
    Office.context.mailbox.item.getSelectedDataAsync(
      Office.CoercionType.Text,
      (result: Office.AsyncResult<any>) => {
        if (result.status === Office.AsyncResultStatus.Failed) {
          if (result.value != undefined) { 
            setError(result.error.message);
          }
        } else {
          setSelection(result.value.data);
        }
      }
    );
  };

  const onToggleCommunication = () => {
    if (communication) {
      setCommunication(false);
    } else {
      setCommunication(true);
    }
  };

  const onInsertText = async (text: string) => {
    Office.context.mailbox.item.body.setSelectedDataAsync(
      text,
      { coercionType: Office.CoercionType.Text },
      (asyncResult) => {
        if (asyncResult.status === Office.AsyncResultStatus.Failed) {
          setError(asyncResult.error.message);
        }
      }
    );
  };

  const getInsertTextButton = (text: string) => {
    return (
      <div role="button" className="btn btn-outline-primary btn-sm" onClick={() => onInsertText(text)}>
        {_t("Insert")}
      </div>
    );
  };

  const getSendButton = () => {
    return (
      <div role="button" className="btn btn-primary" onClick={onSend}>
        {processing ? (
          <span>
            {_t("Processing")}&nbsp;<i className="fa fa-spinner fa-spin"></i>
          </span>
        ) : (
          <span>
            {_t("Send")}&nbsp;<i className="fa fa-send-o"></i>
          </span>
        )}
      </div>
    );
  };

  const promptArea = (
    <>
      {communication && (
        <span className="badge rounded-pill bg-primary me-2 mb-2" onClick={onToggleCommunication}>
          <i className="fa fa-search"></i>
          &nbsp;{_t("Communication")}&nbsp;<i className="fa fa-times"></i>
        </span>
      )}
      {selection && (
        <span className="badge rounded-pill bg-primary me-2 mb-2">
          <i className="fa fa-search"></i>
          &nbsp;{_t("Selection")}
        </span>
      )}
      {addedCase && (
        <span className="badge rounded-pill bg-primary me-2 mb-2" onClick={onRemoveCase}>
          <i className="fa fa-search"></i>&nbsp;{selectedCase.name}&nbsp;<i className="fa fa-times"></i>
        </span>
      )}
      <div>
        <textarea
          className="form-control"
          rows={5}
          placeholder="Type a prompt or load a template..."
          value={prompt}
          onClick={() => checkSelection()}
          onChange={(e) => setPrompt(e.target.value)}
        />
      </div>
      {addCase && (
        <div className="card">
          <div className="card-body">
            <Case cases={cases} selectedCase={selectedCase} onCaseSelected={onCaseSelected} />
            <div className="d-flex justify-content-between mt-3">
              <button title="Add" type="button" className="btn btn-primary btn-sm" onClick={onAddedCase}>
                {_t("Add")}
              </button>
              <button title="Cancel" type="button" className="btn btn-default btn-sm" onClick={onRemoveCase}>
                <i className="fa fa-times text-primary"></i>
              </button>
            </div>
          </div>
        </div>
      )}
      {loadPromptTemplate && (
        <div className="card">
          <div className="card-body">
            <PromptTemplate
              promptTemplates={promptTemplates}
              selectedPromptTemplate={selectedPromptTemplate}
              onPromptTemplateSelected={onPromptTemplateSelected}
            />
            <div className="d-flex justify-content-between mt-3">
              <button title="Add" type="button" className="btn btn-primary btn-sm" onClick={onLoadedPromptTemplate}>
                {_t("Add")}
              </button>
              <button
                title="Cancel"
                type="button"
                className="float-end btn btn-default btn-sm"
                onClick={onRemovePromptTemplate}
              >
                <i className="fa fa-times text-primary"></i>
              </button>
            </div>
          </div>
        </div>
      )}
      <div className="btn-toolbar justify-content-between mt-3" role="toolbar">
        <div className="btn-group" role="group">
          <button title="Return" className="btn btn-default" onClick={() => goToPage(ComposeMainPages.ComposeMainPage)} type="button">
            <i className="fa fa-arrow-left text-primary"></i>
          </button>
          <button
            title="Toggle communication"
            className="btn btn-default"
            onClick={onToggleCommunication}
            type="button"
          >
            <i className="fa fa-comments-o text-primary"></i>
          </button>
          <button title="Add case" className="btn btn-default" onClick={onAddCase} type="button">
            <i className="fa fa-folder-o text-primary"></i>
          </button>
          <button title="Load template" className="btn btn-default" onClick={onLoadPromptTemplate} type="button">
            <i className="fa fa-user-o text-primary"></i>
          </button>
        </div>
        <span>{getSendButton()}</span>
      </div>
    </>
  );

  const chatArea = (
    <>
      {history
        .slice()
        .reverse()
        .map(function (message) {
          if (message.role == "user") {
            return (
              <div key={message.content}>
                <div className="alert alert-success mt-3 mb-3" role="alert">
                  {message.content}
                </div>
              </div>
            );
          } else if (message.role == "assistant") {
            return (
              <div key={message.content}>
                <div className="alert alert-primary mt-3 mb-3" role="alert">
                  <div dangerouslySetInnerHTML={{ __html: message.content }}></div>
                  <div className="mt-3">{getInsertTextButton(message.content)}</div>
                </div>
              </div>
            );
          } else if (message.role == "system") {
            return (
              <div key={message.content}>
                <div className="alert alert-danger mt-3 mb-3" role="alert">
                  {message.content}
                  <div className="mt-3">{getInsertTextButton(message.content)}</div>
                </div>
              </div>
            );
          } else {
            return false;
          }
        })}
    </>
  );

  const introArea = (
    <div className="alert alert-primary mt-3 mb-3" role="alert">
      {_t(
        "Hi, I'm the Innoverius AI Assistant, here to help! Feel free to ask me a question or give me an instruction to draft or rewrite text. You can also specify a case or select text in your document, so I can use it as context for your request."
      )}
    </div>
  );

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-magic fa-fw text-primary"></i>&nbsp;{_t("Generate Text")}
      </div>
      <Error error={error} setError={setError} />
      {promptArea}
      {chatArea}
      {introArea}
      <Progress processing={processing} />
    </div>
  );
};

export default GenerateAdvancedAiTextPage;