import React, { useCallback } from "react";
import { _t } from "../../../utils/Translator";
import api from "../../api";
import SearchTagSelect from "../searchselect/SearchTagSelect";
import { TagItem } from "../tags/tag";

/* global Office */

export interface PartyItem {
  name: string;
  id: number;
  caseId: number;
  email: string;
  tags: TagItem[];
}

interface PartyProps {
  parties: PartyItem[];
  selectedParty: PartyItem;
  onPartySelected: (p: PartyItem) => void;
}

const Party: React.FC<PartyProps> = ({ parties, selectedParty, onPartySelected }) => {
  const addTo = useCallback((party: PartyItem) => {
    Office.context.mailbox.item.to.addAsync([{
      emailAddress: party.email,
      displayName: party.name,
    }]);
  }, []);

  const addCC = useCallback((party: PartyItem) => {
    Office.context.mailbox.item.cc.addAsync([{
      emailAddress: party.email,
      displayName: party.name,
    }]);
  }, []);

  const addBCC = useCallback((party: PartyItem) => {
    Office.context.mailbox.item.bcc.addAsync([{
      emailAddress: party.email,
      displayName: party.name,
    }]);
  }, []);

  const getAddToButton = () => (
    <div role="button" id="addTo" className="btn btn-outline-primary btn-sm" onClick={() => addTo(selectedParty)}>
      <i className="fa fa-plus-circle"></i>&nbsp;{_t("To")}
    </div>
  );

  const getAddCCButton = () => (
    <div role="button" id="addCC" className="btn btn-outline-primary btn-sm ms-2" onClick={() => addCC(selectedParty)}>
      <i className="fa fa-plus-circle"></i>&nbsp;{_t("Cc")}
    </div>
  );

  const getAddBCCButton = () => (
    <div role="button" id="addBCC" className="btn btn-outline-primary btn-sm ms-2" onClick={() => addBCC(selectedParty)}>
      <i className="fa fa-plus-circle"></i>&nbsp;{_t("Bcc")}
    </div>
  );

  const getPartyButtons = () => {
    if (selectedParty) {
      return (
        <>
          {getAddToButton()}
          {getAddCCButton()}
          {getAddBCCButton()}
        </>
      );
    }
    return null;
  };

  const getPartnerButton = () => {
    if (selectedParty) {
      const url = `${api.baseURL}/web#id=${selectedParty.id}&model=res.partner&view_type=form`;
      return (
        <a href={url} target="_blank" rel="noreferrer">
          <i className="fa fa-external-link" aria-hidden="true"></i>
        </a>
      );
    } else {
      return null;
    }
  };

  return (
    <div>
      <div className="page-section-title">{_t("Party")}&nbsp;{getPartnerButton()}</div>
      <SearchTagSelect
        items={parties}
        selectedItem={selectedParty}
        onItemSelected={onPartySelected}
        selectText={_t("Select party")}
        searchText={_t("Search party")}
      />
      <div className="mt-3">
        {getPartyButtons()}
      </div>
    </div>
  );
};

export default Party;