import * as React from "react";
import { useState, useEffect } from "react";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { ComposeMainPages } from "../composemain/ComposeMain";
import Record, { RecordItem } from "../record/Record";
import MailTemplate, { MailTemplateItem } from "../mail_template/MailTemplate";
import Error from "../error/Error";
import Progress from "../progress/Progress";

interface GenerateMailPageProps {
  mailTemplates: MailTemplateItem[];
  getConnectionToken: () => string;
  goToPage: (page: ComposeMainPages) => void;
}

const GenerateMailPage: React.FC<GenerateMailPageProps> = (props: GenerateMailPageProps) => {
  const {
    mailTemplates,
    getConnectionToken,
    goToPage,
  } = props;
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);
  const [records, setRecords] = useState<RecordItem[]>([]);
  const [selectedRecord, setSelectedRecord] = useState<RecordItem>(null);
  const [selectedMailTemplate, setSelectedMailTemplate] = useState<MailTemplateItem>(null);

  const onGenerateMail = async () => {
    setProcessing(true);
    let response = await getMailTemplateData(selectedMailTemplate, selectedRecord);
    if (response.result.success) {
      response.result.data.mail_template_data.subject && Office.context.mailbox.item.subject.setAsync(response.result.data.mail_template_data.subject);
      response.result.data.mail_template_data.body_html && Office.context.mailbox.item.body.setAsync(response.result.data.mail_template_data.body_html, {coercionType: Office.CoercionType.Html});
      response.result.data.mail_template_data.email_to && Office.context.mailbox.item.to.setAsync(response.result.data.mail_template_data.email_to, () => {});
      response.result.data.mail_template_data.email_cc && Office.context.mailbox.item.cc.setAsync(response.result.data.mail_template_data.email_cc, () => {});
    }
    setProcessing(false);
  };

  const getRecords = async (mailTemplate: MailTemplateItem) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getRecordData,
        ContentType.Json,
        getConnectionToken(),
        { template_id: mailTemplate && mailTemplate.id },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    }
    return response; 
  };

  const getMailTemplateData = async (mailTemplate: MailTemplateItem, record: RecordItem) => {
    let response = null;
    try {
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getMailTemplateData,
        ContentType.Json,
        getConnectionToken(),
        { mail_template_id: mailTemplate.id, record_id: record && record.id },
        true
      );
      response = JSON.parse(await request.promise);
    } catch (error) {
      setError(String(error));
    } 
    return response;
  }

  useEffect(() => {
    selectedMailTemplate && getRecords(selectedMailTemplate).then((response) => {
      if (response.result.success) {
        setRecords(response.result.data.records);
      } else {
        setError(response.result.error);
      }
    });
  }, [selectedMailTemplate]);
  
  const getCancelButton = () => {
    return (
      <button title="Return" type="button" className="btn btn-default" onClick={() => goToPage(ComposeMainPages.ComposeMainPage)}>
        <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
      </button>
    );
  };

  const getGenerateMailButton = () => {
    return (
      <button title="Confirm" type="button" className="btn btn-primary" onClick={onGenerateMail}>
        {_t("Confirm")}
      </button>
    );
  };
  
  return (
    <div>
      <div className="page-title">
        <i className="fa fa-pencil-square-o fa-fw text-primary"></i>&nbsp;{_t("Generate Mail")}
      </div>
      <Error error={error} setError={setError} />
      <MailTemplate
        mailTemplates={mailTemplates}
        selectedMailTemplate={selectedMailTemplate}
        onMailTemplateSelected={setSelectedMailTemplate}
      />
      <Record records={records} selectedRecord={selectedRecord} onRecordSelected={setSelectedRecord} />
      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        <span>{getGenerateMailButton()}</span>
      </div>
      <Progress processing={processing} />
    </div>
  );
};

export default GenerateMailPage;