import * as React from "react";
import { TagItem } from "../tags/tag";
import Tags from "../tags/tags";
import api from "../../api";
import { _t } from "../../../utils/Translator";

import { PublicClientApplication } from "@azure/msal-browser";

export interface MailItem {
  subject: string;
  id: string;
}

export interface MailProps {
  mail: MailItem;
  tags: TagItem[];
}

export interface MailState {
  added: boolean;
  mail_subject: string;
}

export default class Mail extends React.Component<MailProps, MailState> {
  tagsRef;
  msalConfig = {
    auth: {
      clientId: "5aede471-24cc-46ff-be56-7b6442567ca5",
      authority: "https://login.microsoftonline.com/common",
      redirectUri: api.addInBaseURL + "/taskpane.html",
    },
    cache: {
      cacheLocation: "sessionStorage",
      storeAuthStateInCookie: true,
    },
  };
  msalInstance = new PublicClientApplication(this.msalConfig);
  constructor(props, context) {
    super(props, context);
    this.state = {
      added: true,
      mail_subject: (props.mail && props.mail.subject) || "",
    };
    this.tagsRef = React.createRef();
  }

  componentDidUpdate(prevProps) {
    // Ensure prevProps.mail and this.props.mail are defined before accessing their properties
    if (prevProps.mail && this.props.mail && prevProps.mail.id !== this.props.mail.id) {
      this.setState({
        mail_subject: this.props.mail.subject || "",
      });
    }
  }
 
  resetMail = () => {
    this.setState({
      added: true,
      mail_subject: (this.props.mail && this.props.mail.subject) || "",
    });
    if (this.tagsRef.current) {
      this.tagsRef.current.resetTags();
    }
  };

  _arrayBufferToBase64 = (buffer) => {
    var binary = "";
    var bytes = new Uint8Array(buffer);
    var len = bytes.byteLength;
    for (var i = 0; i < len; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa(binary);
  };

  getAccessToken = async () => {
    let accessTokenResponse;
    if (this.msalInstance.getAllAccounts().length > 0) {
      let request = {
        scopes: ["Mail.Read"],
        account: this.msalInstance.getAllAccounts()[0],
      };
      accessTokenResponse = await this.msalInstance.acquireTokenSilent(request);
    }
    if (accessTokenResponse && accessTokenResponse.accessToken) {
      return accessTokenResponse.accessToken;
    } else {
      let request = {
        scopes: ["Mail.Read"],
      };
      accessTokenResponse = await this.msalInstance.acquireTokenPopup(request);
      return accessTokenResponse.accessToken;
    }
  };

  getMailGraphData = async () => {
    let access_token = await this.getAccessToken();
    let graph_url = "https://graph.microsoft.com/v1.0/me/messages/" + this.props.mail.id + "/$value";
    let response = await fetch(graph_url, {
      headers: {
        Authorization: `Bearer ${access_token}`,
      },
    });
    let buffer = await response.arrayBuffer();
    return this._arrayBufferToBase64(buffer);
  };

  getMailData = async () => {
    if (!this.state.added) {
      return {};
    }
    let mail_data = await this.getMailGraphData();
    return {
      mail: this.props.mail,
      data: mail_data,
      subject: this.state.mail_subject,
      tags: this.tagsRef.current.getTagsData(),
    };
  };

  add = () => {
    this.setState({ added: true, mail_subject: (this.props.mail && this.props.mail.subject) || "" });
  };

  remove = () => {
    this.setState({ added: false });
  };

  render() {
    if (this.state.added) {
      return (
        <div>
          <div className="page-section-title">{_t("Mail")}</div>
          <div className="card">
            <div className="card-body">
              <div className="input-group mt-3">
                <input
                  className="form-control"
                  type="text"
                  value={this.state.mail_subject}
                  onChange={(e) => this.setState({ mail_subject: e.target.value })}
                />
                <button
                  className="btn btn-primary"
                  type="button"
                  aria-label={_t("Close")}
                  onClick={this.remove}
                >
                  <i className="fa fa-trash" aria-hidden="true" />
                </button>
              </div>
              <div className="mt-2">
                <Tags ref={this.tagsRef} tags={this.props.tags}></Tags>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <>
          <div className="page-section-title">{_t("Mail")}</div>
          <div className="mt-3">
            <button className="btn btn-outline-primary btn-sm" type="button" onClick={this.add}>
              <i className="fa fa-plus-circle" aria-hidden="true"></i>&nbsp;{_t("Add e-mail")}
            </button>
          </div>
        </>
      );
    }
  }
}
