import * as React from "react";

export interface SearchItem {
  name: string;
  id: number;
}

// TODO We make the assumption the items contain "name" and "id" keys.
// Is there a way to enforce this?
export interface SearchSelectProps {
  items: SearchItem[];
  selectedItem: SearchItem;
  onItemSelected: (i: SearchItem) => void;
  selectText: string;
  searchText: string;
}

export interface SearchSelectState {
  searchInput: string;
}

export default class SearchSelect<T extends SearchSelectProps> extends React.Component<T, SearchSelectState> {
  constructor(props) {
    super(props);
    this.state = {
      searchInput: "",
    };
  }

  filterItem = (item: SearchItem, searchInput: string) => {
    return item.name.toLowerCase().includes(searchInput) && (!this.props.selectedItem || item.id != this.props.selectedItem.id);
  }

  filterItems = (items: SearchItem[], searchInput: string) => {
    let filteredItems = items.filter((item) => this.filterItem(item, searchInput));
    return filteredItems;
  };

  getItemHTML = (item: SearchItem) => {
    return (
      <a className="dropdown-item" href="#">
        {item.name}
      </a>
    );
  };

  getItemsToShow = (items: SearchItem[]) => {
    let itemsToShow = [];
    let searchInputLower = this.state.searchInput.toLowerCase();
    let filteredItems = this.filterItems(items, searchInputLower);
    for (let i of filteredItems) {
      let id = i.id;
      itemsToShow.push(
        <li key={id} onClick={() => this.props.onItemSelected(i)}>
          {this.getItemHTML(i)}
        </li>
      );
    }
    return itemsToShow;
  };

  render() {
    const { items, selectedItem, selectText, searchText } = this.props;
    let itemsToShow = this.getItemsToShow(items);
    return (
      <div>
        <div className="btn-group searchselect-dropdown" role="group">
          <div className="form-select searchselect-dropdown-button" role="button" data-bs-toggle="dropdown">
            <div className="searchselect-dropdown-button-text">
              {selectedItem ? selectedItem.name : selectText}
            </div>
          </div>
          <ul className="dropdown-menu dropdown-menu-right dropdown-menu-wide">
            <li className="searchselect-input" key={"searchselect-input"}>
              <a>
                <input
                  className="dropdown-item"
                  type="text"
                  value={this.state.searchInput}
                  onChange={(e) => this.setState({ searchInput: e.target.value })}
                  placeholder={searchText}
                />
              </a>
              <hr className="dropdown-divider"></hr>
            </li>
            {itemsToShow}
          </ul>
        </div>
      </div>
    );
  }
}
